
.holder{
   display: flex;
   flex-direction: column;
   justify-content: center;
}.circle{
   width: 100vw;
   height: 7vh;
   background-color: #ffffff;
   border-bottom-left-radius: 80%;
   border-bottom-right-radius: 80%;
   display: flex;
   justify-content: center;

}

.circle-add {
   height: 6rem;
   width: 6rem;
   background-color: #c73e1d;
   background-image: linear-gradient(315deg, #c73e1d 0%, #a23b72 37%, #2e86ab 100%);

   border-radius: 50%;
   position: absolute;
   margin-top: 2vh;
   display: flex;
   justify-content: center;
}
.circle-add:hover {
   background-image: linear-gradient(315deg, #e48c76 0%, #e674b0 37%, #6ac9f1 100%);
   background-color: rgba(255, 255, 255, 0.479);
   box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
   transition: 0.2s;
}

.fi-xwluxl-plus-wide{
   position: relative;
   font-size: 3rem;
   color: white;
   margin-top: .9rem;
}


.reminder-holder {
   display: flex;
   flex-direction: column;
   justify-content: center;
   margin-top: 90px;
}

.reminder{
   display: flex;
   flex-direction: row;
   align-self: center;
   justify-content: space-between;
   background-color: white;
   width: 91vw;
   height: 200px;
   margin-bottom: 1rem;
   border-radius: 10px;

}
.reminder:hover , .reminder:focus {
   width: 92vw;
   cursor: pointer;
   box-shadow: 0px 13px 74px -15px rgb(179, 179, 179);
   transition: 0.2s;
}

.reminder-info {
   display: flex;
   flex-direction: column;
   margin-left: 18px;
   margin-top: 12px;
}
.reminder-info p{
   font-family:'Varela Round', sans-serif; 
   color: black;
}


/* Time on The bOX and AM PM*/

.time{
   font-size: 2rem;
}
.time span {
   font-size: 1rem;
}

/*  */
.message {
   max-width: 100%;
   max-height: 40px;
   margin-top: 6rem;
   font-size: 1rem;
}

.reminder-tools {
   display: flex;
   flex-direction: column;
   justify-content: space-between;
}
.reminder-tools .fa-trash {
   color: #404040;
   margin-top: 12px;
   margin-right: 18px;
   float: right;
   font-size: 1.5rem;
}
.reminder-tools .fa-bell {
   margin-top: 12px;
   margin-right: 18px;
   float: right;
   font-size: 1.5rem;
}
.reminder-tools .fa-trash:hover{
   color: grey;
}



/* Colour Changer Circles */


.colour-changer{
  
   height: 30px;
   width: 30px;
   background-color: #663dff;
   background-image: linear-gradient(319deg, #663dff 0%, #aa00ff 37%, #cc4499 100%);
   border-radius: 50%;
   margin-bottom: 1.2rem;
   margin-left: 2rem;
   
}



/* New reminder DIV */
.new-reminder {
   max-height: 0px;
   
   width: 91vw;
   background-color: white;
   align-self: center;
   margin-top: 2rem;
   display: flex;
   flex-direction: column;
   justify-content: center;
   box-shadow: 0px 13px 74px -15px rgb(179, 179, 179);
   overflow: hidden;
   
}
.react-date-picker__wrapper{
   margin-top: 2rem;
   height: 40px;
}
react-date-picker--open react-date-picker--enabled{
   height: 40px;
}
react-date-picker--closed react-date-picker--enabled{
   height: 40px;
}

.timeSelector {
   margin-top: 30px;
   align-self: center;
   display: flex;
   flex-direction: row;
   width: 300px;
   justify-content: space-evenly;
}
.dateSelector{
   margin-top: 2rem;
   align-self: center;
   display: flex;
   flex-direction: row;
   width: 300px;
   justify-content: space-evenly;
}
.hourSelector{
  
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   height: 150px;
}



.hourSelector i {
   align-self: center;
   color: #d8cdf6;
   font-size: 20px;
   cursor: pointer;
   transition: color .25s linear;
 }
 .hourSelector i:hover {
   color: #7a6ba3;
 }
 .timeBox {
   width: 80px;
   height: 80px;
   background-color: #cfcfcf;
   border-radius: 1.6rem;
   text-align: center;
   line-height: 100px;
   color: #fff;
   -webkit-box-shadow: 0px 13px 74px -15px rgb(216, 35, 186);
   -moz-box-shadow: 0px 13px 74px -15px rgb(233, 10, 222);
   box-shadow: 0px 13px 74px -15px rgb(179, 179, 179);
 }

 .monthBox{
    margin:0 1rem;
   width: 30vw;
   height: 80px;
   background-color: #cfcfcf;
   border-radius: 1.6rem;
   text-align: center;
   line-height: 100px;
   color: #fff;
   -webkit-box-shadow: 0px 13px 74px -15px rgb(216, 35, 186);
   -moz-box-shadow: 0px 13px 74px -15px rgb(233, 10, 222);
   box-shadow: 0px 13px 74px -15px rgb(179, 179, 179);
 }
 .yearBox{
   width: 110px;
   height: 80px;
   background-color: #cfcfcf;
   border-radius: 1.6rem;
   text-align: center;
   line-height: 100px;
   color: #fff;
   -webkit-box-shadow: 0px 13px 74px -15px rgb(216, 35, 186);
   -moz-box-shadow: 0px 13px 74px -15px rgb(233, 10, 222);
   box-shadow: 0px 13px 74px -15px rgb(179, 179, 179);
 }



 /* Switch */

.switchText{
   font-family:'Varela Round', sans-serif; 
   font-size: 1.2rem;
   margin-bottom: 10px;
   left: -10%;
}
 .alarmBox{
    align-self: center;
    margin-top: 15px;
 }
 .hidden {
   position: absolute !important;
   top: -9999px !important;
   left: -9999px !important;
 }
 

 
 #switch {
   background-color: grey;
   width: 80px;
   height: 40px;
   display: inline-block;
   border-radius: 20px;
   cursor: pointer;
   transition: background-color 0.5s;
   margin-left: 16px;
 }
 
 #switch::before {
   content: "";
   display: inline-block;
   width: 35px;
   height: 35px;
   background-color: white;
   border-radius: 50%;
   position: relative;
   left: 3px;
   transition: left 0.5s;
   top:3px;
 }
 
 #switchcheckbox:checked + label {
   background-color: rgb(112, 255, 112);
 }
 
 #switchcheckbox:checked + label::before {
   left: 42px;
 }


 /* ColourBox*/
 .colorBox{
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    align-self: center;
    width: 80vw;
    height: 40px;
    justify-content: space-evenly;
 }

 .colorCircle{
    
    height: 35px;
    width: 35px;
    border-radius: 50%;
    cursor: pointer;


 }


 /* Save Button */ 
 .saveButton{
    align-self: center;
   font-family: 'Varela Round', sans-serif; 
   border: none;
   background: #404040;
   color: #ffffff !important;
   font-weight: 100;
   font-size: 1rem;
   padding: 15px;
   text-transform: uppercase;
   border-radius: 25px;
   display: inline-block;
   transition: all 0.3s ease 0s;
   margin-left: 1rem;
   margin-top: 1.5rem;
   margin-bottom: 1.5rem;
   width: 75px;
 }
 .saveButton:hover {
   color: #404040 !important;
   font-weight: 700 !important;
   
   
   background: none;
   -webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
   -moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
   transition: all 0.3s ease 0s;
   cursor: pointer;
}


 /*Media Queries*/

 @media screen and (max-width:375px){
   .timeBox {
      width: 60px;
      height: 60px;
   }
   .monthBox {
      width: 120px;
      height: 60px;
   }
   .yearBox {
      width: 90px;
      height: 60px;
   }
   .hourSelector{
      height: 110px;
   }
 }
 
